<template>
<div class="customer-content">
<van-list
  v-model="customerList['loading']"
  finished-text="没有更多了"
  :finished="customerList['finished']"
  @load="onLoad"
  :offset="10"
>
	<!--列表-->
	<customer-item v-for="(item,index) in customerList.list" :customer="item" :key="index" @click.native="itemClick(item.id)" @updateClick="updateClick(item)" @deleteClick="deleteClick(item,index)"/>
</van-list>
<div class="addCustomer" @click="addCustomer">+</div>
</div>
	
</template>

<script>

import { List } from 'vant';
import CustomerItem from './CustomerItem'

export default {
  name: 'CustomerContent',
  components:{
	[List.name]:List,
	CustomerItem
  },
  data(){
	return{
	}
  },
  props:{
	customerList:{
		type:Object,
		default(){
			return {}
		}
	}
  },
  methods:{
	onLoad(){
		this.$emit('onLoad')
	},
	//详细点击
	itemClick(id){
		//放入缓存
		localStorage.setItem("customerdetailid",id);
		//console.log(id)
		//这个id刷新有问题 需要保存到缓存里面
		this.$router.push({name:'customerdetail',params:{id:id}}).catch(res=>{res})
	},
	//添加商品
	addCustomer(){
		this.$router.push('/addcustomer').catch(res=>{res})
	},
	//子组件的修改点击
	updateClick(obj){
		this.$emit('updateClick',obj.id)
	},
	//子组件的删除点击
	deleteClick(obj,index){
		this.$emit('deleteClick',obj.id,index)
	}
  }
}
</script>

<style scoped>
*{
	margin:0;
	padding:0;
}

.customer-content{
    position: relative;
	height:calc(100% - 133px);
	background:#f7f7f7;
    right: 0;
    left: 0;
	overflow:auto;
}
.addCustomer{
	position:fixed;
	bottom:80px;
	right:30px;
	border-radius:25px;
	border:1px solid #8fa2ff;
	width:40px;
	height:40px;
	background:#8fa2ff;
	font-size:18px;
	color:white;
	text-align:center;
	line-height:40px;
	z-index:99;
}
</style>
