<template>
<div id="customer">
	<!--2-->
	<van-nav-bar
		title="客户管理"
		class="customer-nav"
	/>
	<!--搜索框-->
	<search-box @changeKeys="changeKeys" @searchclick="searchclick"/>
	<!--内容-->
	<customer-content @onLoad="onLoad" :customer-list="currentCustomer" @updateClick="updateClick"  @deleteClick="deleteClick"/>
</div>
	
</template>

<script>
import { NavBar,List  } from 'vant'
import {getCustomer,deleteCustomer} from 'network/customer'
import SearchBox from 'components/common/searchbox/SearchBox'
import CustomerContent from './childComps/CustomerContent'

export default {
  name: 'Customer',
	components:{
		[NavBar.name]:NavBar,
		[List .name]:List ,
		SearchBox,
		CustomerContent
	},
	data(){
		return{
			customerObj:{
				customerIndex:{
					page:0,
					list:[],
					loading: false,
					refreshing:false,
					finished: false,
					total:0,				//同类型数据一共有多少条
					time:0,
				}
			},
			currentType:'customerIndex',
		}
	},
	methods:{
		//发送数据
		searchclick(keywords){
			//再次发送获取数据
			this.getCustomer(keywords)
		},
		//获取数据
		getCustomer(keywords=''){
			let customer=this.customerObj['customerIndex']
			let page=customer.page+1
			
			//获取数据
			getCustomer(keywords,page).then(res=>{
				customer.loading=false
				if (res == null || res.length === 0) {
					// 加载结束
						customer.finished = true;
						return;
					}
					customer.total=res[0]['count']
					customer.list.push(...res)				//放入数据
					customer.page+=1
					//如果列表数据条数>=总条数，不再触发滚动加载
					if (customer.list.length >= customer.total) {
						customer.finished = true;
					}
			})
		},
		//防抖
		debounce(fn, wait = 500){
			if (window.timeout) clearTimeout(window.timeout)
				window.timeout = setTimeout(() => {
					fn()
				}, wait)
		},
		//搜索框的修改内容按钮
		changeKeys(keywords){
			this.debounce(()=>{
				//继续加载
				if(keywords==""){
					this.customerObj['customerIndex'].finished = false
				}
				//将数组清空
				this.customerObj['customerIndex'].list=[]
				//重置页码
				this.customerObj['customerIndex'].page=0
				//再次发送获取数据
				this.getCustomer(keywords)
			})
		},
		//修改
		updateClick(id){
			//缓存写入一个修改的id
			localStorage.setItem('updateCustomerid',id)
			//跳转到修改页面
			this.$router.push('/updatecustomer').catch(res=>{res})
		},
		//删除
		deleteClick(id,index){
			//console.log(id+"先来一个弹窗再 确定再删除")
			let _self=this				//指针指向
			this.$dialog.confirm({
				title: '警告',
				message:
					'请注意该操作不可恢复,是否继续删除该条数据?',
				})
				.then(() => {
					//删除数据库里的(数据库有必要给一个删除成功与否的标识)
					//先删除数据库里面的在删除数组的
					deleteCustomer(id).then(res=>{
						setTimeout(()=>{
							//删除原来那个数组选中下标的那个数据
							_self.customerObj['customerIndex'].list.splice(index,1)		//删除数组index下边的值
							_self.$toast(res)
						},800)
					})
					// on confirm
					//这里确定了就删除指定数组的下标数据
				})
				.catch(() => {
					// on cancel
					_self.$toast('取消')
			});
			
		},
		//子页面加载数据
		onLoad(){
			this.getCustomer()
		}
	},
	computed:{
		currentCustomer(){
			return this.customerObj['customerIndex']
		}
	},
	//挂载数据
	mounted(){
		//this.getCustomer()
		//console.log(this.customerObj['customerIndex'].list)
	},
	activated(){
		console.log('activated');
	},
	deactivated(){
		let scrollTop = document.body.scrollTop
		console.log(scrollTop+'deactivated');
	}
}
</script>

