import {request} from './request'

export function getCustomer(keywords='',page=0,time=0) {
  return request({
	url:'/vuedata/customer.php',
    params:{
		keywords,
		page,
		time,
    }
  });
}

//获取一个客户
export function getCustomerDetail(id) {
  return request({
	url:'/vuedata/customerdetail.php',
    params:{
		id,
    }
  });
}
//添加一个客户
export function addCustomer(customer) {
  return request({
	url:'/vuedata/addcustomer.php',
    params:{
		customer,
    }
  });
}

//修改一个客户
export function updateCustomer(customer) {
  return request({
	url:'/vuedata/updateCustomer.php',
    params:{
		customer,
    }
  });
}

//删除一个客户
export function deleteCustomer(id) {
  return request({
	url:'/vuedata/deleteCutomer.php',
    params:{
		id,
    }
  });
}